/* ==============================
   Footer
   ============================== */

.footer{
    text-align: center;
    font-size: 12px;
    a {
        color: #888;
        text-decoration: none;
        transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        &:hover{
            color: #111;
        }
    }
    &.bg-gray-lighter {
        a {
            &:hover{
                color: $gray-bg-color;
            }
        }
    }
}

.footer-copy{
    margin-bottom: 5px;
    text-transform: uppercase;
    letter-spacing: 0.5em;
    .footer-contacts {
        &__item {
            padding-bottom: 3px;
            @media only screen and (min-width: 768px) {
                padding-bottom: 5px;
            }
        }
        &__email {
            text-transform: none;
        }
    }
}
.footer-made{
    font-size: 11px;
    color: #999;
}


.footer-social-links{
    font-size: 14px;
    a {
        width: 25px;
        height: 25px;
        line-height: 25px !important;
        position: relative;
        margin: 0 6px;
        text-align: center;
        display: inline-block;
        color: #111;

        -webkit-transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        -moz-transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        -o-transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        -ms-transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        i, span {
            position: relative;
            top: 0;
            left: 0;
        }
        &:before {
            content: "";
            display: inline-block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            border: 1px solid #111;
            border-radius: 2px;
            transform: rotate(45deg);
            transition: all 0.27s cubic-bezier(0.300, 0.100, 0.580, 1.000);
        }
        &:hover {
            color: #fff;
            &:before {
                background: #111;
            }
        }
    }
}
.bg-gray-lighter {
    .footer-social-links{
        a {
            color: $gray-color;
            &:before {
                border: 1px solid $gray-color;
            }
            &:hover {
                color: $gray-bg-color;
                &:before {
                    border: 1px solid $gray-bg-color;
                    background: $black-color;
                }
            }
        }
