/*
 * Tabs
 */

.tpl-tabs{
    border-color: #e5e5e5;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1px;
}
.tpl-tabs > li > a{
    text-decoration: none;
    color: $black-color;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
}
.tpl-tabs > li > a:hover{
    background-color: #f5f5f5;
}
.tpl-tabs li.active a{
    color: #000;
}
.tpl-tabs-cont{
    padding: 30px 0;
    color: $black-color;
    line-height: 1.8;
}

/*
 * Tabs minimal
 */

.tpl-minimal-tabs{
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1px;
    border: none;
}
.tpl-minimal-tabs > li{
    padding: 0 30px;
    background: none !important;
}
.tpl-minimal-tabs > li > a{
    padding: 6px 20px 5px;
    text-decoration: none;
    color: #000;
    background: none;
    border: 1px solid transparent;

    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
}
.tpl-minimal-tabs > li > a:hover{
    background: none;
    border-color: $borders-color;
    color: $black-color;
}
.tpl-minimal-tabs li.active a,
.tpl-minimal-tabs li.active a:hover{
    border: 1px solid #222 !important;
    background: none !important;
    color: #000;
    cursor: default;
}
.tpl-minimal-tabs-cont{
}


/*
 * Tabs Alternative
 */

.tpl-alt-tabs{
    font-size: 14px;
